  .dial .dial-value {
    z-index: -10;
    text-align: center;
    position: absolute;
    width: 100%;
    top: 40px; }
  .dial .dial-stroke {
    z-index: -10;
    width: 1px;
    height: 10%;
    position: absolute;
    margin: 0 auto;
    top: calc(50% - 5% - 2px);
    padding: 2px;
    left: calc(50% - 2px);
