.buttons-container label {
  display: inline-block; }

.buttons-container section {
  display: inline-block; }

.buttons-container button {
  color: #c9c9c9;
  font-family: 'Nunito', sans-serif;
  padding: 10px;
  margin: 5px;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0);
  border: none; }

.buttons-container .sorting-algorithms button {
  box-shadow: inset -5px -5px 5px 0 rgba(0, 0, 0, 0.2), inset 5px 5px 5px 0 rgba(255, 255, 255, 0.03); }

.buttons-container .sorting-options button {
  box-shadow: 5px 5px 5px 0 rgba(0, 0, 0, 0.2), -5px -5px 5px 0 rgba(255, 255, 255, 0.03); }

.buttons-container .speed-option-container {
  margin-right: 10%; }

